import React from "react";
import { RouteComponentProps } from 'react-router';
import Connection, { getArgFromLocation } from '../Connection';

interface NavigatorCanShare extends Navigator {
    canShare?: (data?: ShareData) => boolean;
}

interface Props extends RouteComponentProps<{}> {
}

export default class ResultScreen extends React.PureComponent<Props> {
    apikey = '';
    connection?: Connection;

    state = {
        closed: false,
        resultFilename: '',
    };

    componentDidMount() {
        this.apikey = getArgFromLocation(this.props.location, 'apiKey');
        this.connection = new Connection(this.apikey);
        this.connection.on('onStarted', this.onStarted);
        this.connection.on('onDisconnect', this.onDisconnect);

        const resultFilename = getArgFromLocation(this.props.location, 'resultFilename');
        this.setState({resultFilename});
    }

    componentWillUnmount() {
        this.connection?.off('onStarted', this.onStarted);
        this.connection?.off('onDisconnect', this.onDisconnect);
    }

    render() {
        const {closed, resultFilename} = this.state;
        return (
            <div className="container onepage_container result_container">
                <div className="logo">
                    <img
                        src="./logo.svg"
                        alt="logo"
                    />
                </div>
                <div className="instructions">
                    <span className="uppercase">Partagez votre photo</span>
                </div>
                <div className="camera">
                <img
                    id="resultImg"
                    src={`https://cartier.vista-api.net/uploads/cartier/${resultFilename}`}
                    alt="result"
                    />
                </div>
                {
                    !closed &&
                    <button className="iconBtn" onClick={this.restart}>Recommencez
                        <img
                            src="./restart.png"
                            alt="restart"
                        />
                    </button>
                }
                <div className="btn" onClick={this.send}>
                    <button>Recevoir par email</button>
                </div>
            </div>
        );
    }

    onFinish = () => {
        const waiting = false;
        this.setState({waiting});
    }

    onStarted = () => {
        const { history } = this.props;
        history.replace(`./control?apiKey=${this.apikey}`);
    }

    onDisconnect = () => {
        const closed = true;
        this.setState({closed});
    }

    restart = () => {
        const closed = true;
        this.setState({closed});

        this.connection?.start();
    }

    send = () => {
        const { history } = this.props;
        const { resultFilename } = this.state;
        history.push(`./send?apiKey=${this.apikey}&resultFilename=${resultFilename}`);
    }

    share = async () => {
        const nav = navigator as NavigatorCanShare;
        const img = document.getElementById('resultImg') as HTMLImageElement;
        // level 2 share
        if (nav.canShare) {
            const res = await fetch(img.src);
            const blob = await res.blob();
            const file = new File([blob], 'Cartier Tank Experience.jpg', blob)
            const shareData = {
                // title: 'Cartier Tank Experience',  breaks on iOS
                files: [file],
            } as ShareData;
            if (nav.canShare(shareData)) {
                nav.share(shareData);
                return;
            }
        }

        // level 1 share
        if (nav.share) {
            nav.share({
                title: 'Cartier Tank Experience',
                url: img.src,
            });
        }
        else {
            console.error(`Your system doesn't support sharing.`);
        }
    }
}
