import React, { FormEvent } from "react";
import { RouteComponentProps } from 'react-router';
import pRetry from "p-retry";
import countryList from 'react-select-country-list';
import Connection, { getArgFromLocation } from '../Connection';

interface Props extends RouteComponentProps<{}> {
}

const CGU = "En cliquant sur « S'abonner », je souhaite recevoir des informations sur les produits ou services de Cartier. Nous pouvons vous envoyer ces informations par e-mail, sms ou téléphone. Nous pouvons également utiliser vos données pour diffuser des messages ou de la publicité personnalisés sur les réseaux sociaux ou d’autres plateformes numériques. Vous pouvez nous demander de cesser la prospection commerciale à tout moment. Pour plus de renseignements sur la façon dont nous utilisons vos données à caractère personnel, veuillez consulter notre Politique de Confidentialité.";
const SEND_URI = "https://google.fr";

export default class ResultScreen extends React.PureComponent<Props> {
    connection?: Connection;
    resultFilename = '';
    countries = countryList().getData();

    userCiv = React.createRef<HTMLSelectElement>();
    userFirstname = React.createRef<HTMLInputElement>();
    userLastname = React.createRef<HTMLInputElement>();
    userEmail = React.createRef<HTMLInputElement>();
    userPostcode = React.createRef<HTMLInputElement>();
    userCity = React.createRef<HTMLInputElement>();
    userCountry = React.createRef<HTMLSelectElement>();
    userSubscribe = React.createRef<HTMLInputElement>();

    state = {
        disabled: false,
        error: '',
    }

    componentDidMount() {
        this.resultFilename = getArgFromLocation(this.props.location, 'resultFilename');

        const apikey = getArgFromLocation(this.props.location, 'apiKey');
        this.connection = new Connection(apikey);
    }

    componentWillUnmount() {
    }

    render() {
        const { disabled, error } = this.state;
        return (
            <div className="container">
                <div className="logo">
                    <img
                        src="./logo.svg"
                        alt="logo"
                    />
                </div>
                <div className="instructions">
                    <span>Recevoir votre<br />photo par email</span>
                </div>
                <div>
                    <form onSubmit={this.submit}>
                        <div className="form-step">
                            <label htmlFor="civ">Civilité *</label>
                            <select id="civ" name="user_civ" defaultValue="" required ref={this.userCiv}>
                                <option disabled hidden value="">Choisir</option>
                                <option value="Mlle">Madame</option>
                                <option value="Mme">Mademoiselle</option>
                                <option value="M">Monsieur</option>
                            </select>
                        </div>
                        <div className="form-step">
                            <label htmlFor="lastname">Nom *</label>
                            <input type="text" id="lastname" name="user_lastname" required ref={this.userLastname} />
                        </div>
                        <div className="form-step">
                            <label htmlFor="firstname">Prénom *</label>
                            <input type="text" id="firstname" name="user_firstname" required ref={this.userFirstname} />
                        </div>
                        <div className="form-step">
                            <label htmlFor="email">Adresse email *</label>
                            <input type="email" id="email" name="user_email" required ref={this.userEmail} />
                        </div>
                        <div className="form-step">
                            <label htmlFor="postcode">Code postal *</label>
                            <input type="text" id="postcode" name="user_postcode" required ref={this.userPostcode} />
                        </div>
                        <div className="form-step">
                            <label htmlFor="city">Ville *</label>
                            <input type="text" id="city" name="user_city" required ref={this.userCity} />
                        </div>
                        <div className="form-step">
                            <label htmlFor="country">Pays *</label>
                            <select id="country" name="user_country" defaultValue="FR" required ref={this.userCountry} >
                            {
                                this.countries.map((element, key) => {
                                    return <option key={key} value={element.value}>{element.label}</option>;
                                })
                            }
                            </select>
                        </div>
                        <div className="cgu">
                            <span>{CGU}</span>
                        </div>
                        <div className="form-step form-step-cgu">
                            <input type="checkbox" id="cgu" name="cgu" ref={this.userSubscribe} />
                            <label htmlFor="cgu">S'abonner</label>
                        </div>
                        <div className="btn">
                            <button type="submit" id="submit" disabled={disabled}>Valider</button>
                            <label htmlFor="submit" className="error">{error}</label>
                        </div>
                        <div className="cgu">
                            <span>* Champs obligatoires</span>
                        </div>
                    </form>
                </div>
            </div>
        );
    }

    submit = async (event: FormEvent) => {
        event.preventDefault();

        const civ = this.userCiv.current?.value;
        const lastname = this.userLastname.current?.value;
        const firstname = this.userFirstname.current?.value;
        const email = this.userEmail.current?.value;
        const postcode = this.userPostcode.current?.value;
        const city = this.userCity.current?.value;
        const country = this.userCountry.current?.value;
        const subscribe = this.userSubscribe.current?.checked;

        if (civ && lastname && firstname && email && postcode && city && country) {
            this.setState({
                error: '',
                disabled: true,
            });

            const formData = new FormData();
            formData.append('civ', civ);
            formData.append('lastname', lastname);
            formData.append('firstname', firstname);
            formData.append('email', email);
            formData.append('postcode', postcode);
            formData.append('city', city);
            formData.append('country', country);
            formData.append('subscribe', subscribe ? 'true' : 'false');
            formData.append('image', this.resultFilename);

            try {
                const response = await pRetry(() => {
                    return fetch(SEND_URI, {
                        method: 'POST',
                        body: formData,
                    });
                }, {
                    retries: 3,
                });

                if (response.status >= 200 && response.status < 300) {
                    this.close();
                    return;
                }
                else {
                    const error = response.statusText;
                    this.setState({error});
                }
            }
            catch(err) {
                this.setState({error: err.message || err.toString()});
            }

            this.setState({disabled: false});
        }
    }

    close = () => {
        this.connection?.disconnect();

        const { history } = this.props;
        history.replace('./');
    }
}
