import React from "react";
import { RouteComponentProps } from 'react-router';
import Connection, { getArgFromLocation } from '../Connection';
import CameraImage from './CameraImage';

const PHOTO_COUNT = 3;

interface Props extends RouteComponentProps<{}> {
}

const INSTRUCTION1 = "Prenez votre\n%1 photo";
const INSTRUCTION2 = "Appuyez sur\nle bouton PRESS";


export default class ControlScreen extends React.PureComponent<Props> {
    apikey = '';
    connection?: Connection;

    state = {
        canTakePhoto: true,
        photoNumber: 0,
    };

    componentDidMount() {
        this.apikey = getArgFromLocation(this.props.location, 'apiKey');
        this.connection = new Connection(this.apikey);
        this.connection.on('onPhotoTaken', this.onPhotoTaken);
        this.connection.on('onFinish', this.onFinish);
        this.connection.on('onDisconnect', this.onDisconnect);
    }

    componentWillUnmount() {
        this.connection?.off('onPhotoTaken', this.onPhotoTaken);
        this.connection?.off('onFinish', this.onFinish);
        this.connection?.off('onDisconnect', this.onDisconnect);
    }

    render() {
        const { photoNumber, canTakePhoto } = this.state;
        const ith = ['1ère', '2ème', '3ème'][photoNumber];
        const instruction1 = INSTRUCTION1.replace('%1', ith);
        return (
            <div className="container onepage_container">
                <div className="logo">
                    <img
                        src="./logo.svg"
                        alt="logo"
                    />
                </div>
                <div className="camera">
                    <div className="camera_btn">
                        <button
                            onClick={this.takePhoto.bind(this)}
                            disabled={!canTakePhoto}
                        >{canTakePhoto && "PRESS"}</button>
                    </div>
                    <img
                        src={CameraImage}
                        alt="camera"
                    />
                </div>
                <div className="instructions control_instructions">
                    { canTakePhoto &&
                    <>
                        <span className="uppercase">{instruction1}</span>
                        <span>{INSTRUCTION2}</span>
                    </>
                    }
                </div>
            </div>
        );
    }

    takePhoto() {
        this.connection?.takePicture();

        const canTakePhoto = false;
        this.setState({canTakePhoto});
    }

    onPhotoTaken = () => {
        const canTakePhoto = true;
        const photoNumber = this.state.photoNumber + 1;
        if (photoNumber < PHOTO_COUNT) {
            this.setState({canTakePhoto, photoNumber});
        }
    }

    onFinish = (resultFilename: string) => {
        const { history } = this.props;
        history.replace(`./result?apiKey=${this.apikey}&resultFilename=${resultFilename}`);
    }

    onDisconnect = () => {
        const { history } = this.props;
        history.replace('./');
    }
}
