import React from "react";
import { RouteComponentProps, withRouter } from 'react-router';
import Connection, { getArgFromLocation } from '../Connection';

const RESET_STARTING_TIMEOUT = 2000;

interface Props extends RouteComponentProps<{}> {
}

class HomeScreen extends React.PureComponent<Props> {
    apikey = '';
    connection?: Connection;
    resetStartingTimeout?: NodeJS.Timeout;

    state = {
        starting: false
    }

    componentDidMount() {
        this.apikey = getArgFromLocation(this.props.location, 'apiKey');
        if (!!this.apikey) {
            this.connection = new Connection(this.apikey);
            this.connection.on('onStarted', this.onStarted);
            this.start();
        }
    }

    componentWillUnmount() {
        if (this.resetStartingTimeout) {
            clearTimeout(this.resetStartingTimeout);
        }
        this.connection?.off('onStarted', this.onStarted);
    }

    render() {
        const { starting } = this.state;
        return (
            <div className="container onepage_container">
                <div className="logo">
                    <img
                        src="./logo.svg"
                        alt="logo"
                    />
                </div>
                <div className="instructions">
                    <span className="uppercase">
                        { starting ? 'Connexion...' : 'Scannez le QR code' }
                    </span>
                </div>
            </div>
        );
    }

    start() {
        this.connection?.start();

        const starting = true;
        this.setState({starting});

        if (this.resetStartingTimeout) {
            clearTimeout(this.resetStartingTimeout);
        }
        this.resetStartingTimeout = setTimeout(this.resetStarting.bind(this), RESET_STARTING_TIMEOUT);
    }

    resetStarting() {
        if (this.resetStartingTimeout) {
            clearTimeout(this.resetStartingTimeout);
            this.resetStartingTimeout = undefined;
        }

        const starting = false;
        this.setState({starting});
    }

    onStarted = () => {
        const { history } = this.props;
        history.replace(`./control?apiKey=${this.apikey}`);
    }
}

export default withRouter(HomeScreen);
