import { Socket } from "socket.io";
// @ts-ignore
import io from "socket.io-client";
import {Location} from 'history';

type EventNames = 'onStarted' | 'onPhotoTaken' | 'onFinish' | 'onDisconnect';

export default class Connection {
    _socket: Socket;
    _apikey: string;

    constructor(apikey: string) {
        this._apikey = apikey;
        const anyWindow = window as any;
        if (anyWindow.sharedSocket === undefined) {
            anyWindow.sharedSocket = {};
        }
        this._socket = anyWindow.sharedSocket[apikey];
        if (!this._socket) {
            console.log(`new connection to ${apikey}`);
            this._socket = io('https://cartier.vista-api.net/cartier', {
                query: {apikey},
                transports: ['websocket'],
                reconnectionAttempts: 15
            });
            anyWindow.sharedSocket[apikey] = this._socket;
        }

        this._socket.on('connect', this.onConnected.bind(this));
        this._socket.on('error', this.onError.bind(this));
    }

    public start() {
        this._socket.emit('start');
    }

    public takePicture() {
        this._socket.emit('takePicture');
    }

    public disconnect() {
        console.log('disconnect');
        this._socket.disconnect(true);

        const anyWindow = window as any;
        if (anyWindow.sharedSocket && anyWindow.sharedSocket[this._apikey]) {
            delete anyWindow.sharedSocket[this._apikey];
        }
    }

    public on(event: EventNames, listener: (...args: any[]) => void) {
        this._socket.on(event, listener);
    }

    public off(event: EventNames, listener: (...args: any[]) => void) {
        this._socket.off(event, listener);
    }

    onConnected() {
        console.log('connected!');
    }

    onError(err: any) {
        console.error(err);
    }
}

export function getArgFromLocation(location: Location<unknown>, argName: 'apiKey' | 'resultFilename'): string {
    const parts = location.search.split(`${argName}=`);
    if (parts.length < 2) {
        console.error(`An ${argName} query param is required`);
        return '';
    }

    const right = parts[1];
    return right.split('&')[0];
};
